html, body {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
  background-color: black;
}

.App {
  text-align: center;
}

#car {
  position: relative;
}

#wheel1 {
  position: absolute;
  left: 215px;
  top: 300px;
}

#wheel2 {
  position: absolute;
  left: 925px;
  top: 300px;
}

.accelerating {
  -webkit-animation: acc 2s linear, acc1 1s linear 2s, acc2 0.6s linear 3s infinite;
          animation: acc 2s linear, acc1 1s linear 2s, acc2 0.6s linear 3s infinite;
}

.deaccelerating {
  -webkit-animation: brake 1s linear;
          animation: brake 1s linear;
}

#acc {
  float: left;
  margin-top: 30px;
  margin-left: 100px;
}

#brake {
  float: right;
  margin-top: 30px;
  margin-right: 100px;
}

.paddle:hover {
  cursor: pointer;
}

@-webkit-keyframes acc {
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}

@keyframes acc {
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}

@-webkit-keyframes acc1 {
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}

@keyframes acc1 {
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}

@-webkit-keyframes acc2 {
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}

@keyframes acc2 {
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}

@-webkit-keyframes brake {
    0%   {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}

@keyframes brake {
    0%   {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100% {-webkit-transform: rotate(-360deg);transform: rotate(-360deg);}
}


@media only screen and (max-width: 576px) {
  #brake {
    float: left;
    margin-top: 30px;
    margin-left: 100px;
  }
}



